"use client";

import Header from "@/components/Header";
import HeroSection from "@/components/HeroSection";
import WhyRegisterSection from "@/components/WhyRegisterSection";
import UpcomingEventsSection from "@/components/UpcomingEventsSection";
import HowItWorksSection from "@/components/HowItWorksSection";
import NewsletterSection from "@/components/NewsletterSection";
import Footer from "@/components/Footer";
import { useState } from "react";

export default function Home() {
  const [showProgress, setShowProgress] = useState(false); // State to control progress visibility
    const [loggedInUser, setLoggedInUser] = useState<string | null>(null); // Track logged-in user email
  return (
    <div className="bg-gray-100">
      {/* Progress Ring */}
            {showProgress && (
              <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-70 z-60">
                <div className="loader"></div>
                <span className="text-white mt-4">Loading...</span>
              </div>
            )}
      
            {/* Header */}
            <Header
              setShowProgress={setShowProgress}
              loggedInUser={loggedInUser}
              setLoggedInUser={setLoggedInUser}
            />
      <HeroSection />
      <WhyRegisterSection />
      <UpcomingEventsSection />
      <HowItWorksSection />
      <NewsletterSection />
      <Footer />
    </div>
  );
}